body{
    .popup {
        position: absolute;
        top: 0;
        left: 0;
        background: rgba($color: #000000, $alpha: 0.8);
        width: 100%;
        height: 100%;
        z-index: 30;
        display: none;
    }

    &.header-open .popup {
        display: block;
    }
}